@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap);
.card {
  box-shadow: 0 4px 8px 0 rgba(131, 60, 60, 0.2);
  transition: 0.3s;
  width: 30%;
}

#e-card {
  background-color: #d48e54;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(199, 190, 190, 0.3);
}

img {
  width: 100%;
}

.experience {
  margin: 5%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.title {
  text-align: center;
  color: rgb(98, 27, 139);
  padding: 3%;
  margin: 3%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.designation {
  text-align: center;
  color: rgb(98, 27, 139);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.description {
  text-align: center;
  color: rgb(71, 5, 109);
  padding-left: 7%;
  padding-right: 7%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.year {
  text-align: center;
  color: rgb(71, 5, 109);
  padding-left: 7%;
  padding-right: 7%;
  padding-bottom: 20px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

@media screen and (min-width: 1100px) {
  .card {
    width: 30%;
    margin: 5%;
  }
  .experience {
    margin: 5%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
  }
}

@media screen and (max-width: 1100px) and (min-width: 630px) {
  .card {
    width: 40%;
    margin: 5%;
  }
  .experience {
    margin: 5%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
  }
}

@media screen and (max-width: 630px) {
  .card {
    width: 70%;
    margin: 5%;
  }
  .experience {
    margin: 5%;
    display: -webkit-flex;
    display: flex;
  }
}

